<!-- 论坛首页、分类 -->
<template>
	<view class="tabs">
		<u-navbar :fixed="true" :safeAreaInsetTop="true" title="分类" @rightClick="goPost()">
			<view class="u-nav-slots" slot="left">
			</view>
			<view class="u-nav-slots" slot="right">
				<u-icon name="plus-circle" color="#b0b0b0" size="50rpx"></u-icon>
			</view>
		</u-navbar>
		<view class="navbarheight"></view>
		<!-- <view class="navbarheight"></view> -->
		<view class="p20 bgw">
			<u-swiper :list="Recommend" keyName="image" height="355rpx" radius="20rpx" showTitle :autoplay="false" circular @click="goSwiper">
			</u-swiper>
			
		</view>
		<div class="c-title"><text style="position:absolute;left: 17px;top: 6px;">软件分类</text></div>
					
		<u-empty v-if="!showlist" mode="sort" icon="http://cdn.uviewui.com/uview/empty/data.png"></u-empty>
		<u-grid v-else col="2" border align="left" >
			<u-grid-item v-for="(item,baseListIndex) in catList" :key="baseListIndex" bgColor="#ffffff" @click="goSortList(item.name,item.id)">
				<!-- <u-avatar slot="icon" shape="square" size="35" :src="item.icon"	customStyle="margin: -3px 5px -3px 0"></u-avatar> -->
				<image class="br10" :src="item.imgclass" ></image>
				<text class="grid-text">{{item.name}}</text>
			</u-grid-item>
		</u-grid>
		<u-no-network></u-no-network>
	</view>
</template>
<script>
	import API from '../../utils/api.js';
	export default {
		data() {
			return {
				Recommend:[],				
				catList:[],
				showlist:''
			}
		},
		onLoad() {
			this.getCat();
			this.getRecommend();
		},
		methods: {
			goPost:function(){
				var posts =this.Recommend[0].posts;
				console.log(posts)
				if(posts === '0'){
					var key = this.Recommend[0].qqfurm;
					plus.runtime.openURL(
						'mqqopensdkapi://bizAgent/qm/qr?url=http%3A%2F%2Fqm.qq.com%2Fcgi-bin%2Fqm%2Fqr%3Ffrom%3Dapp%26p%3Dandroid%26jump_from%3Dwebapi%26k%3D' +
					key)
				}else{
					uni.navigateTo({
						url: '/subPage/commen/post'
					});
				}
			},
			goSwiper:function(e){
				var a =e;
				var cids = this.Recommend[a].post[0].cid;
				var name = this.Recommend[a].post[0].title;
				uni.navigateTo({
					url: '/subPage/commen/appdetails?cid=' + cids +'&name=' +name
				});
				console.log(cids)
			},
			getRecommend: function() {
				uni.request({
					url: API.GetRecommend(),
					success: res => {
						console.log(res)
						this.Recommend = res.data.data;
						// console.log(res.data.data.postlunbo[0].str_value);
					}
				});
			},
			getCat: function() {
				console.log(API.GetCat())
				uni.request({
					url: API.GetCat()
				}).then(res => {
					var ress = res[1].data;
					if (typeof(ress) != 'undefined'&&ress != "none"&&ress!="NULL") {
						this.showlist =true;
						this.catList = res[1].data
						console.log(this.catList)
					}else{
						this.showlist=false;
					}
				})
			},
			goSortList: function(name,id) {
				uni.navigateTo({
					url: "/pages/appsort/appsortlist?category=" + name+"&id="+id
				})
			},
		}
	}
</script>
<style>
	page{
		background-color: #f4f4f4;
	}
	.u-nav-slots {
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-width: 0.5px;
		border-radius: 100px;
		border-color: $u-border-color;
		padding: 3px 7px;
		opacity: 1;
		/* width: 60%; */
	}
	.p20 {
		padding: 20rpx;
	}
	.pl20 {
		padding-left: 20rpx;
	}
	.pb10 {
		padding-bottom: 10rpx;
	}
	.bgw{
		background-color: #FFFFFF;
	}
	.grid-text{
		margin-left: 10rpx;
	}
	.br10{
		border-radius: 10rpx;
		width: 90rpx;
		height: 90rpx;
	}
	.navbarheight {
		height: 44px;
		padding-top: var(--status-bar-height);
	}
	.c-title{
		
		font-size: 15px;
		font-weight: 600;
		color: #333;
		background-color: #f6f6f6;
		height:50rpx;
		line-height: 30rpx;
		position: relative;
		padding-left:$u * 5;
		padding-top: $u * 4;
		box-sizing:content-box;

	}
	.c-title::after {
	    position: absolute;
	    content: "";
	    left: 7px;
	    top: 6px;
	    height: 12px;
	    width: 3px;
	    border-radius: 30px;
	    background-color: #999;
	    z-index: 2;
	}
</style>
